body{
  background-color: black;
}

.App {
  text-align: center;
  color: white;
}

.Republican{
  background-color: rgb(196, 0, 0);
}

.Democratic{
  background-color: rgb(9, 0, 128);
}
.Unknown-nonpartisan{
  background-color: rgb(67, 67, 77);
}
.Green-party{
  background-color: rgb(13, 116, 17);
}

.candidate{
  border-radius: 10px;
  margin: 20px;
  padding: 10px;
  max-width: 300px;
  display: inline-block;
  vertical-align: top;
  height: auto;
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}
.candidate h3{

text-align: center;
margin-bottom: 5px;
}

h2{
  border-bottom: 3px solid white;
}

button{ 
  margin: 5px;
  background: white;
  width: 5em;
  height: 2em;
}

.image-container{
  width: 180px;
  height: 180px;
  margin: 0 auto;
  border: 3px solid white;
  border-radius: 5px;
  background-size: cover;
  background-position: center;
}

.image-container img{
  width: 100%;
  
}

.social {
  border-top: 2px solid white;
}

.social a, .website{
  display: inline-block;
  padding: 10px;
  color: white;
  border: 2px solid white;
  font-size: 1em;
  width: 1em;
  height: 1em;
  border-radius: 100px;
  margin: .5em;
  vertical-align: middle;
  line-height: 1em;
}

.social a:hover,.social a:focus, .website:focus, .website:hover{
  opacity: .8;
}

.info{
  max-width: 100%;
  text-align: left;
  padding: 0px 10px;
  margin: 10px 0px;
}

#register, #polls{
  color: white;
  font-weight: bold;
  text-decoration: none;
  background-color: rgba(21, 59, 210, 1);
  padding: 20px 30px;
  display: inline-block;
  margin: 20px;
  margin-bottom:20px;
  border-radius: 5px;
}

#register:hover, #register:focus, #polls:hover, #polls:focus{
  background-color: rgba(21, 59, 210, .5);
}

.readmore, a{
  color: white;
  font-weight: bold;
}
.top-p{
  max-width: 600px;
  margin: 30px auto;
  padding: 0px 20px;
  
}

.hidden 
{position:absolute;
left:-10000px;
top:auto;
width:1px;
height:1px;
overflow:hidden;}

#footer{
  border-top: 2px solid white;
  font-size: .8em;
  padding: 20px;
}


